import React from 'react'

function Cart(props) {
    const { cartItems, onAdd, onRemove } = props;
    //calculating each items price
    const itemsPrice = cartItems.reduce((a, c) => a + c.qty * c.price, 0)
    //calculating total tax
    const taxPrice = itemsPrice * 0.2;
    //calculating shipping cost
    const shippingPrice = itemsPrice < 2000 ? 0 : 200
    //finally total price
    const totalPrice = itemsPrice + taxPrice + shippingPrice;
    return (
        <div>
            <div className="container">
                <h1 className="text-center display-4 mb-5">Cart</h1>
                {
                    cartItems.length == 0 && <div className='display-4'>Your cart is empty</div>
                    
                }
                <table className="table table-bordered table-stripped">
                    <thead className="bg-dark text-white">
                        <tr>
                            <td>ID</td>
                            <td>Name</td>
                            <td>Image</td>
                            <td>Price</td>
                            <td></td>
                            <td>Actions</td>
                        </tr>
                    </thead>
                    <tbody>
                {
                    cartItems.map((item) => (
                        <tr>
                            <td>{item.id}</td>
                            <td>{item.name}</td>
                            <td><img className="img-responsive img-fluid" src={item.image} /></td>
                            <td>{item.price}</td>
                            <td>Calculation: {item.qty} x {item.price}</td>
                            <td colSpan="2" className="text-center">
                                <button className="btn btn-primary mr-4" onClick={()=>{onAdd(item)}}>+</button>
                                <button className="btn btn-danger" onClick={()=>{onRemove(item)}}>-</button>
                            </td>
                        </tr>
                    ))
                }

                    </tbody>
                </table>
                {
                    cartItems.length !== 0 && (
                        <>
                            <div className="">                          
                                <div className="text-right"><h5>Total : {itemsPrice}</h5></div>
                                <div className="text-right"><h5>Tax : {taxPrice}</h5></div>
                                <div className="text-right"><h5>Shipping : {shippingPrice}</h5></div>
                                <div className="text-right"><h5>Grand Total : {totalPrice}</h5></div>
                            </div>
                        </>
                    )
                }
                    
            </div>
        </div>
    )
}

export default Cart
