import React from 'react'

function Main(props) {
    const { products, onAdd } = props
    return (
        <div>
            <div className="container">
                <h1 className="text-center display-3 mt-5 mb-5">All Products</h1>
                <div className="row">
                    {
                        products.map(pro => (
                            <div className="col-md-3 mb-5">
                                <div className="card">
                                    <img className="card-img-top" src={pro.image} />
                                    <div className="card-body">
                                        <h5 className="card-title">{pro.name}</h5>
                                        <p className="lead">{pro.price}</p>
                                        <hr />
                                        <button onClick={() => { onAdd(pro) }} className="btn btn-outline-primary">Add to Cart</button>
                                    </div>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div >
    )
}

export default Main
