import React, { useState } from "react";
import data from './data';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from './Components/Header';
import Main from './Components/Main';
import Cart from "./Components/Cart";

function App() {
  const { products } = data;

  //hook for getting data into cart
  const [cartItems, setCartItems] = useState([]);

  //function to add product in cart
  const onAdd = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist) {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty + 1 } : x
        )
      );
      console.log(cartItems);
    } else {
      setCartItems([...cartItems, { ...product, qty: 1 }]);
      console.log(cartItems);
    }
  };

  //function to remove product from cart
  const onRemove = (product) => {
    const exist = cartItems.find((x) => x.id === product.id);
    if (exist.qty === 1) {
      setCartItems(cartItems.filter((x) => x.id !== product.id));
    } else {
      setCartItems(
        cartItems.map((x) =>
          x.id === product.id ? { ...exist, qty: exist.qty - 1 } : x
        )
      );
    }
  };

  return (
    <div>

      <Router basename={process.env.PUBLIC_URL} >
        <Header cartItems={cartItems.length} />
        <Switch>
          <Route exact path="/" render={() =>
            <>
              <Main products={products} onAdd={onAdd} />
            </>
          } />
        </Switch>

        <Switch>
          <Route path="/cart" render={() =>
            <>
            {
              cartItems.length !==0 ? <Cart onAdd={onAdd} onRemove={onRemove} cartItems={cartItems} /> : <h2 className="display-4 mt-5 text-center">Your Cart is empty <br /> <br />(@ - __ - @)</h2>
            }
              
            </>
          } />
        </Switch>
      </Router>

    </div>
  );
}

export default App;
